import React, { useEffect, useRef, useState } from "react";
import { Chart as ChartJS, registerables } from "chart.js";

// 
ChartJS.register(...registerables);

const ChartComponent = ({ feeds }) => {
  const chartRef = useRef(null); //  useRef  chart instance
  const [allFeeds, setAllFeeds] = useState([]); //  ThingSpeak

  useEffect(() => {
    //  
    if (feeds.length > 0) {
      // 
      setAllFeeds((prevFeeds) => [...prevFeeds, ...feeds]);
    }
  }, [feeds]); //  feeds 

  useEffect(() => {
    if (allFeeds.length > 0) {
      const labels = allFeeds.map(feed => new Date(feed.created_at).toLocaleTimeString());
      const values = allFeeds.map(feed => parseFloat(feed.field1));

      const ctx = chartRef.current.getContext("2d");

      //  
      if (chartRef.current.chartInstance) {
        chartRef.current.chartInstance.destroy();
      }

      // 
      chartRef.current.chartInstance = new ChartJS(ctx, {
        type: "line",
        data: {
          labels: labels,
          datasets: [
            {
              label: "",
              data: values,
              borderColor: "blue",
              borderWidth: 2,
              fill: false,
            },
          ],
        },
        options: {
          responsive: true,
          scales: {
            y: {
              beginAtZero: true,
            },
          },
        },
      });
    }
  }, [allFeeds]); //  allFeeds 

  return <canvas ref={chartRef} id="myChart"></canvas>; //  ref  id
};

export default ChartComponent;
